import React from 'react';
import {Link} from 'react-router-dom';
import './header.css';

function Header(){
    return(
        <header 
        className='main-header'>

<div className='logo-red'>
                <img src={require('../img/logo-redfox.png')}/>
                </div>

            <div className='header-content'>
                
                <Link to='/'>
                    Home
                </Link>
                
            </div>
        </header>
    );
}

export default Header;