import React, {Component} from 'react';
import firebase from '../../firebase';
import {Link} from 'react-router-dom';
import './home.css';

class Home extends Component {
            constructor(){
                super();
                this.state = {
                    poke:[]
                
            }
        this.chamaPoke = this.chamaPoke.bind(this);
        this.Delete = this.Delete.bind(this);
             
       
            }
            
            componentDidMount(){
                this.chamaPoke();
            }

            chamaPoke(){
               firebase.database().ref('Pokemon').on('value', (snapshot)=>{
                    let state = this.state;
                    state.poke = [];

                    snapshot.forEach((element) => {
                        state.poke.push({
                            id: element.key,
                            name: element.val().Name,
                            def: element.val().Def,
                            image: element.val().Img,
                            atk: element.val().Atk
                        })
                    });

                    this.setState(state)
               })
               
            }

            Delete(id, name){
                let  ok = window.confirm('Tem certeza que deseja deletar o ' + name +  ' :( ?');

                if(ok === true){
                    firebase.database().ref('Pokemon').child(id).remove();
                    alert('O card foi removido com sucesso! ');
                    window.location.reload()
                }else{
                    alert('Uffa, o '+ name+ ' Agradece :) !!')
                }
            }

        
            



    render(){
        return(
            <div className='home-card'>
                {this.state.poke.map((item)=>{
                    return(
                        
                        <div className='card-solo' key={item.id}>
                        <h1>{item.name}</h1>
                        <img src={item.image} />
                        <div className='power'>
                            <p className='atk'> Atk: {item.atk} </p>
                            <p className='def'> Def: {item.def} </p>
                        </div>
                        <button onClick={()=>this.Delete(item.id, item.name)} className='delete'>X</button>
                        <Link to={`/detalhe/${item.id}`}>detalhe</Link>
                    </div>
                    );
                })}
            
            
            
            </div>

        );
    }



}

export default Home;