import React, {Component} from 'react';
import firebase from '../../firebase';
import './detalhe.css';


class Detalhe extends Component {
            constructor(props){
                super(props);
                this.state = {
                    idparam:'',
                    id:'',
                    name: 'Carregando...',
                    atk: 'Carregando...',
                    def: 'Carregando...',
                    img: '',
                    wallpaper: 'Carregando...',
                    nameInput: '',
                    atkInput: '',
                    defInput: '',

            }
            this.chamaDetalhe = this.chamaDetalhe.bind(this);
            this.update = this.update.bind(this);
       
            }
            componentDidMount(){
                const {id} = this.props.match.params;
                const state = this.state;
                
                state.idparam = id;
                
                this.setState(state)
                    console.log(this.state.idparam)
                this.chamaDetalhe();
            }
            chamaDetalhe(){
            firebase.database().ref('Pokemon').child(this.state.idparam).on('value', (snapshot)=>{
                    let state = this.state;

                    state.id = snapshot.key;
                    state.name = snapshot.val().Name;
                    state.img = snapshot.val().Img;
                    state.atk = snapshot.val().Atk;
                    state.def = snapshot.val().Def;
                    state.wallpaper = snapshot.val().wallpaper;

                    this.setState(state);
                })
                
            }

            update(id){

                if(this.state.nameInput === '' || this.state.atkInput === '' || this.state.defInput === ''){
                    alert('Por favor, revise os campos em branco')
                    
                }else{
                    const state = this.state;
                    const ok = window.confirm('Tem certez que deseja fazer a alteração do item ' + id + ' ? :(');
    
                    if (ok == true) {
                        firebase.database().ref('Pokemon').child(id).set({
                            Name: state.nameInput,
                            Atk: state.atkInput,
                            Def: state.defInput,
                            Img: state.img,
                            wallpaper: state.wallpaper
                        })
                        state.nameInput = '';
                        state.defInput = '';
                        state.atkInput = '';
    
                        alert('Update realizado com sucesso! ')
                    } else {
                        alert('Uffa...')
                    }
                }


                
                
            }
    
        
              
    render(){
        return(

            <div className='container-detalhe'>
              {this.state.id !==0 &&
                <div className='container-update'>
                <label>Nome:
                    <input type='text' placeholder='Novo nome do Pokemon' value={this.state.nameInput}
                    onChange={(e)=>this.setState({nameInput: e.target.value})} />
                </label><br/>

                <label>Attack:
                    <input type='number' placeholder='1234' value={this.state.atkInput}
                    onChange={(e)=>this.setState({atkInput: e.target.value})} />
                </label><br/>

                <label>Defense:
                    <input type='number' placeholder='1234' value={this.state.defInput}
                    onChange={(e)=>this.setState({defInput: e.target.value})}  />
                </label><br/>

                <button onClick={()=>this.update(this.state.id)}>Update</button>

            </div>
              }
              
              <img className='imgBack' src={this.state.wallpaper}/>
            

            
            </div>

        );
    }



}

export default Detalhe;